body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Toolbar {
  z-index: 10;
}

/* Let's get this party started */
::-webkit-scrollbar {
  width: 0.3vw;
}

/* Track */
::-webkit-scrollbar-track {
  background: #070707;
}
::-webkit-scrollbar-track-piece {
  display: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #aaaaaa;
  right: 20px;
}
::-webkit-scrollbar-thumb:window-inactive {
  background: #aaaaaa40;
}

:root {
  --toolbar-height: 80px;
}

.Toolbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 80px;
  height: var(--toolbar-height);
  padding: 0;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  color: white;

  overflow: hidden;
  /* background-color: red; */
}

.ToolbarLogo {
  padding: 22px 20px;
  height: 80px;
  float: left;
  background-color: transparent;

  font-size: 1.7em;

  transition: 0.3s ease-in;
}

.ToolbarLogo:hover {
  background-color: white;
  color: black;
  transition: 0.3s ease-in;
  cursor: pointer;
}

i {
  width: 1.7rem;
  height: 1.7rem;
  font-size: 1.7rem;
  display: block;
  margin: auto;
}

.ToolbarItem {
  display: inline-block;
  line-height: 80px;
  padding: 0px 30px;
  text-transform: uppercase;
  transition: 0.3s ease-in;
}

.ToolbarItem:hover {
  background-color: white;
  color: black;
  transition: 0.3s ease-in;
  cursor: pointer;
}

.ToolbarSpacer {
  display: inline-block;
  line-height: 80px;
  padding: 0px 30px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-transform: uppercase;
  min-width: 0;

  /* background-color: black; */
}

.main {
  margin: 0;
  padding: 0;
  height: 100%;

  /* background-color: blueviolet; */

  color: white;

  /* font-family: "Roboto"; */

  -webkit-perspective: 1px;

          perspective: 1px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  overflow-y: scroll;
  overflow-x: hidden;
}

.Cover * {
  margin: 0;
  padding: 0;
}

.Cover {
  position: relative;
  box-sizing: border-box;

  width: 100vw;
  min-height: 100vh;
  padding: calc(50vh - 3rem) 0 5vw;

  text-align: left;
  font-weight: normal;
  letter-spacing: 0.2em;
}

.Cover h1 {
  /* margin-top: calc(-10vh - calc(3rem / 2)); */
  font-size: 3rem;
  font-weight: 700;
}
.Cover p {
}
.Cover h1,
.Cover p {
  max-width: calc(50vw - calc(50px * 2));
  padding-right: 50px;
  padding-left: 50px;
  cursor: default;
}

.icons a i svg {
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1.5rem;
  display: block;
  margin: auto;
}

.icons {
  padding-top: 20px;
  padding-left: 50px;
  position: absolute;
}
.icons a {
  color: white;
  text-align: center;
}
.icons a i {
  margin: 1rem;
  border-radius: 50%;
  width: 1.3rem;
  height: 1.3rem;
  font-size: 1.5rem;
  color: white;
  vertical-align: middle;
}
.icons a i:hover {
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite;
}

.Cover,
.Cover:before {
  background: 50% 50% / cover;
}

.Cover::before {
  content: "";
  position: absolute;
  display: block;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* height: 100vh; */

  background-image: url(/Porfolio-React/static/media/Cover.ded02c79.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  -webkit-transform-origin: center center 0;

          transform-origin: center center 0;
  -webkit-transform: translateZ(-1px) scale(2);
          transform: translateZ(-1px) scale(2);
  /* overflow: hidden; */
  z-index: -1;
}

::-moz-selection {
  background: #aaa; /* WebKit/Blink Browsers */
}

::selection {
  background: #aaa; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: #aaa; /* Gecko Browsers */
}

.mouse {
  max-width: 2.5rem;
  width: 100%;
  height: auto;
}

.scroll {
  -webkit-animation-name: scroll;
          animation-name: scroll;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: cubic-bezier(0.65, -0.55, 0.25, 1.5);
          animation-timing-function: cubic-bezier(0.65, -0.55, 0.25, 1.5);
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transform-origin: 50% 20.5px;
          transform-origin: 50% 20.5px;
  will-change: transform, opacity;
  opacity: 1;
}

.scroll-link {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

@-webkit-keyframes scroll {
  0%,
  20% {
    -webkit-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
  }

  100% {
    -webkit-transform: translateY(36px) scaleY(2);
            transform: translateY(36px) scaleY(2);
    opacity: 0;
  }
}

@keyframes scroll {
  0%,
  20% {
    -webkit-transform: translateY(0) scaleY(1);
            transform: translateY(0) scaleY(1);
  }

  100% {
    -webkit-transform: translateY(36px) scaleY(2);
            transform: translateY(36px) scaleY(2);
    opacity: 0;
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0.8));
    filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0.8));
  }
  70% {
    -webkit-filter: drop-shadow(0px 0px 15px rgba(255, 255, 255, 0));
    filter: drop-shadow(0px 0px 15px rgba(255, 255, 255, 0));
  }
  100% {
    -webkit-filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0));
    filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0));
  }
}

@keyframes pulse {
  0% {
    -webkit-filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0.8));
    filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0.8));
  }
  70% {
    -webkit-filter: drop-shadow(0px 0px 15px rgba(255, 255, 255, 0));
    filter: drop-shadow(0px 0px 15px rgba(255, 255, 255, 0));
  }
  100% {
    -webkit-filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0));
    filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0));
  }
}

